.main {
  display: table;
  width: 100%;
  height: 100vh;
  text-align: center;
  * {
    transition: all 0.6s;
  }
  body {
    font-family: "Lato", sans-serif;
    color: #888;
    margin: 0;
  }
}

.notfound {
  display: table-cell;
  vertical-align: middle;
  h1 {
    font-size: 50px;
    display: inline-block;
    padding-right: 12px;
    animation: type 0.5s alternate infinite;
  }
}

@keyframes type {
  from {
    box-shadow: inset -3px 0px 0px #888;
  }
  to {
    box-shadow: inset -3px 0px 0px transparent;
  }
}
